.container {
  max-width: 940px;
  padding: 0 15px;
  margin: 0 auto;
}

@media (max-width: 952px) {
  .container {
    max-width: 900px;
  }
}

@media (max-width: 800px) {
  .container {
    max-width: 750px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: none;
    padding: 0 10px;
  }
}






